<template>
  <div class="public-file-upload">
    <FileUpload ref="fileupload" app="forms" :maxFiles="maxFiles" :maxSize="maxSize" :extensions="extensions" @downloadUrl="formBackgroundImage" :deletable="true" />
    <b-img v-if="value" :src="value" fluid alt="Responsive image"></b-img>
  </div>
</template>
<script>
  import FileUpload from "@/components/FileUpload";
  export default {
    name: "FormUpload",
    components: { FileUpload },
    props: {
      value: {
        type: String,
      },
      type: {
        type: String,
      },
      maxFiles: {
        type: String || Number,
      },
      maxSize: {
        type: String,
      },
      extensions: String,
      label: String,
    },
    data() {
      return {
        email: "",
        form_id: "",
      };
    },
    async created() {},
    mounted: function() {
      this.$root.$on("upload_maxFiles", this.upload_maxFiles);
    },
    methods: {
      // After s3 upload these event is emitted
      formBackgroundImage(url) {
        this.$root.$emit("uploaded_files", { [this.label]: url });
      },
      upload_maxFiles(data) {
        this.maxFiles = data;
      },
    },
  };
</script>
